import React from 'react';
import "../../static/css/button.scss";

export default class Button extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            top: 0,
            left: 0
        }

        this.handleMouseEvent = this.handleMouseEvent.bind(this);
    }

    handleMouseEvent(e) {
        return
        var parentOffset = e.target.getBoundingClientRect(),
            relX = e.pageX - (parentOffset.left + document.body.scrollLeft),
            relY = e.pageY - (parentOffset.top + document.body.scrollTop);
        
            console.log(
                (parentOffset.left + document.body.scrollLeft),
                (parentOffset.top + document.body.scrollTop)
            )

            this.setState({
                top:relY,
                left:relX
            })
    }

    render () {
        let span = <span></span>

        if (this.state.top != 0 && this.state.left != 0) {
            span = <span style={{top: this.state.top, left:this.state.left}}></span>
        }

        return <button className="btn-custom"
        onMouseEnter={this.handleMouseEvent}
        onMouseOut={this.handleMouseEvent}>

                    {this.props.children}
                    {span}
                </button>
    }
}